<template>
  <section>
    <v-container fluid>
      <v-row>
        <v-col md="12">
          <PageHeader :title="headercontent.title" />
        </v-col>

        <v-col md="6">
          <v-form
            ref="form"
            v-model="valid"
            lazy-validation
            class="form-section"
          >
            <v-row>
              <v-col cols="12">
                <v-text-field
                  v-model="challengeName"
                  outlined
                  label="Challenge Name"
                  class="mr-btm-minus"
                  :rules="challengeRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="numberOfDays"
                  outlined
                  label="Number of days"
                  class="mr-btm-minus"
                  :rules="challengeRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  v-model="excerpt"
                  outlined
                  rows="5"
                  label="Challenges Summary"
                :counter="150"
                :maxlength="150"
                @keyup="(e)=>e.target.length < 150"
                />
              </v-col>
              <v-col cols="12">
                <v-textarea
                  v-model="description"
                  outlined
                  rows="10"
                  label="Description"
                />
              </v-col>
              <v-col cols="12">
                <v-file-input
                  v-model="image"
                  outlined
                  class="mt-10"
                  placeholder="Add File"
                  accept="image/*"
                />
              </v-col>
              <v-col cols="12">
                <v-text-field
                  v-model="authorName"
                  outlined
                  label="Author Name"
                  class="mr-btm-minus"
                ></v-text-field>
              </v-col>
              <v-col cols="12" v-if="image">
                <img :src="image" alt="" srcset="" />
              </v-col>

              <v-col cols="12" class="submitbtn">
                <v-btn
                  :disabled="!valid"
                  class="submitbutton btn-dark"
                  @click="validate"
                  v-if="!loading"
                >
                  {{ headercontent.title }}
                </v-btn>
                <v-progress-circular
                  v-if="loading"
                  indeterminate
                ></v-progress-circular>
              </v-col>
            </v-row>
          </v-form>
        </v-col>
        <v-col class="questionBox" v-if="this.$route.params.id">
          <h3 class="pb-5">Questions</h3>
          <v-col cols="12">
            <h3 class="mb-5">Add Question</h3>
            <v-text-field
              v-model="question"
              placeholder="Question"
              outlined
            ></v-text-field>
            <v-text-field
              v-model="info"
              placeholder="Info"
              outlined
            ></v-text-field>
            <v-btn
              @click="addQuestion"
              class="mb-5"
              color="green"
              :style="{ color: '#fff' }"
              v-if="!loadingQuestionAdd"
              >Add Question</v-btn
            >
            <v-btn
              @click="addQuestion"
              class="mb-5"
              color="green"
              :style="{ color: '#fff' }"
              v-if="loadingQuestionAdd"
              loading
            ></v-btn>
          </v-col>
          <div v-if="questions.length == 0">No question added yet</div>
          <draggable v-model="questions" class="dragBox cursor-pointer">
            <div
              v-for="(data, index) in questions"
              :key="index"
              class="questionRepeater"
            >
              <div class="header">
                {{ index + 1 }}
                <v-icon> mdi-sort </v-icon>
              </div>
              <v-text-field
                v-model="data.question"
                placeholder="Question"
                outlined
              ></v-text-field>
              <v-text-field
                v-model="data.info"
                placeholder="Info"
                outlined
              ></v-text-field>
              <div class="d-flex justify-space-between">
                <v-btn
                  v-if="loadingUpdateQuestion && index == updateIndex"
                  loading
                  color="blue"
                  :style="{ color: '#fff' }"
                ></v-btn>
                <v-btn
                  v-if="!loadingUpdateQuestion || index != updateIndex"
                  @click="updateQuestion(index)"
                  color="blue"
                  :style="{ color: '#fff' }"
                  >Update</v-btn
                >
                <v-btn
                  @click="removeQuestion(index)"
                  color="red"
                  :style="{ color: '#fff' }"
                  >Remove</v-btn
                >
              </div>
            </div>
          </draggable>
        </v-col>
      </v-row>
    </v-container>
  </section>
</template>

<script>
import PageHeader from "@/components/dashboard/PageHeader";
import { mapActions, mapGetters } from "vuex";
import draggable from "vuedraggable";

export default {
  name: "AddChallenge",
  components: { PageHeader, draggable },
  data: () => ({
    valid: true,
    image: null,
    challengeName: "",
    description: "",
    sort: 0,
    headercontent: {
      title: "Add Challenge",
      addlink: "/dashboard/emotion/add",
      downloadlink: true,
    },
    content: "<h2>I am Example</h2>",
    challengeRules: [(v) => !!v || "Challenge Title is required"],
    loading: false,
    editorOption: {
      // Some Quill options...
    },
    questions: [],
    numberOfDays: 0,
    question: "",
    info: "",
    loadingQuestionAdd: false,
    loadingUpdateQuestion: false,
    updateIndex: undefined,
    authorName: undefined,
    excerpt: undefined,
  }),
  methods: {
    ...mapActions([
      "postChallenge",
      "updateCategory",
      "getChallenge",
      "postChallengeQuestion",
      "deleteChallengeQuestion",
      "updateChallengeQuestion",
    ]),

    async validate() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        let fd = new FormData();
        if (this.$route.params && this.$route.params.id) {
          fd.append("_id", this.$route.params.id);
        }
        fd.append("challengeName", this.challengeName);
        fd.append("description", this.description);
        fd.append("excerpt", this.excerpt);
        fd.append("numberOfDays", this.numberOfDays);
        fd.append("authorName", this.authorName);
        if (this.image != null) {
          fd.append("image", this.image);
          await this.postChallenge(fd);
        } else {
          await this.postChallenge(fd);
        }
        if (this.$route.params.id == undefined) {
          this.$router.push(
            "/dashboard/addchallenge/add/" + this.allChallenge._id
          );
        }
        this.loading = false;
      }
    },

    setImage: function (output) {
      this.hasImage = true;
      this.image = output;
    },

    onEditorChange({ quill, html, text }) {
      console.log(quill, text);
      this.content = html;
    },
    removeImage() {
      this.imageUrl = "";
    },
    async addQuestion() {
      if (this.questions.length <= this.numberOfDays) {
        this.loadingQuestionAdd = true;
        await this.postChallengeQuestion({
          connectedTo: this.$route.params.id,
          question: this.question,
          info: this.info,
        });
        this.question = "";
        this.info = "";
        this.questions = this.allChallenge.questions;
        this.loadingQuestionAdd = false;
      } else {
        alert("You can't add more than " + this.numberOfDays + " questions");
      }
    },
    async removeQuestion(index) {
      await this.deleteChallengeQuestion(this.questions[index]._id);
      this.questions = this.allChallenge.questions;
    },
    async updateQuestion(index) {
      this.updateIndex = index;
      this.loadingUpdateQuestion = true;
      await this.updateChallengeQuestion({
        questionSort: JSON.stringify(this.questions),
      });
      this.questions = this.allChallenge.questions;
      this.loadingUpdateQuestion = false;
      this.updateIndex = undefined;
    },
  },
  computed: {
    ...mapGetters(["allLoadingCurd", "allCategory", "allChallenge"]),
    editor() {
      return this.$refs.myQuillEditor.quill;
    },
  },

  async created() {
    if (this.$route.params && this.$route.params.id) {
      await this.getChallenge(this.$route.params.id);
      this.challengeName = this.allChallenge.challengeName;
      this.authorName = this.allChallenge.authorName;
      this.excerpt = this.allChallenge.excerpt;
      this.description = this.allChallenge.description;
      this.image = this.allChallenge.image;
      this.numberOfDays = this.allChallenge.numberOfDays;
      this.questions = this.allChallenge.questions;
    }
  },
};
</script>

